.Garage {
  height: 800px;
  width: 800px;
  background-image: url("../../assets/garage-background.png");
  background-repeat: no-repeat;
  background-size: contain; }
  .Garage .garage-door {
    position: absolute;
    width: 780px;
    margin: 10px;
    background-image: url("../../assets/garage-door-tile.png");
    background-size: contain;
    -webkit-transition: 0.5s height ease-in-out;
    -o-transition: 0.5s height ease-in-out;
    transition: 0.5s height ease-in-out; }
  .Garage .closed {
    height: 780px; }
  .Garage .open {
    height: 0px; }



